import { isFileTypeImage } from "../../../../../functions/isFileTypeImage";

export function baseFileOnChangeUpdate({
  baseFile,
  setBaseFile,
  setIsDeleteBaseFile,
  setUpdateMessage,
  location,
}) {
  return (e) => {
    if (e.target.files.length && e.target.files[0]) {
      const file = e.target.files[0];
      if (!isFileTypeImage(file.name)) {
        setUpdateMessage("   !");
        return;
      }
      if (file.size > 1048576 * 3) {
        setUpdateMessage("  . (3 mb. max)!");
        return;
      }
      setBaseFile((prev) => ({
        ...prev,
        url: URL.createObjectURL(file),
        file,
      }));
      location && setIsDeleteBaseFile && setIsDeleteBaseFile(location.image);
      e.target.value = "";
    }
  };
}
