export const toClientCB = ({
  setClient,
  setClientStatus,
  clientRef,
  setClientsArr,
  setGetClientMessage,
}) => {
  return (type) => {
    setTimeout(() => {
      if (type === " ") {
        setGetClientMessage("");
        setClient(null);
        setClientStatus("");
      } else if (type === " ") {
        setGetClientMessage("");
        setClient(clientRef.current);
        setClientStatus("");
        setClientsArr([]);
      } else if (type === "") {
        setClientStatus(type);
        setClient(clientRef.current);
      } else {
        setClientStatus(type);
      }
    }, 100);
  };
};
